import React from 'react'

import './skills.scss'

import AOS from 'aos';
import '../../../node_modules/aos/dist/aos.css'

class Skills extends React.Component{
    constructor(props, context) { 
        super(props, context); 
        AOS.init({
            duration: 2000
        }); 
      }
    render(){
    return (
        <div id="skills" className="skills-main-container">
            <div className="skills-bars-container">
                <h2 className="skills-head">Technical &nbsp;Skills</h2>
                <div className="skills-skills">
                    <span className="skills-Name">java</span>
                    <div className="skills-percent">
                        <div data-aos="slide-right" data-aos-mirror="true" data-aos-once="true" style={{width: '70%'}} className="skills-progress"></div>
                    </div>
                    <span className="skills-Value">70%</span>
                </div>
                <div className="skills-skills">
                    <span className="skills-Name">JavaScript</span>
                    <div className="skills-percent">
                        <div data-aos="slide-right" data-aos-mirror="true" data-aos-once="true" style={{width: '85%'}} className="skills-progress"></div>
                    </div>
                    <span className="skills-Value">85%</span>
                </div>
                <div className="skills-skills">
                    <span className="skills-Name">Node js</span>
                    <div className="skills-percent">
                        <div data-aos="slide-right" data-aos-mirror="true" data-aos-once="true" style={{width: '70%'}} className="skills-progress"></div>
                    </div>
                    <span className="skills-Value">70%</span>
                </div>
                <div className="skills-skills">
                    <span className="skills-Name">React JS</span>
                    <div className="skills-percent">
                        <div data-aos="slide-right" data-aos-mirror="true" data-aos-once="true" style={{width: '75%'}} className="skills-progress"></div>
                    </div>
                    <span className="skills-Value">75%</span>
                </div>
                <div className="skills-skills">
                    <span className="skills-Name">mongo db</span>
                    <div className="skills-percent">
                        <div data-aos="slide-right" data-aos-mirror="true" data-aos-once="true" style={{width: '60%'}} className="skills-progress"></div>
                    </div>
                    <span className="skills-Value">60%</span>
                </div>
                <div className="skills-skills">
                    <span className="skills-Name">My sql</span>
                    <div className="skills-percent">
                        <div data-aos="slide-right" data-aos-mirror="true" data-aos-once="true" style={{width: '70%'}} className="skills-progress"></div>
                    </div>
                    <span className="skills-Value">70%</span>
                </div>
                <div className="skills-skills">
                    <span className="skills-Name">HTML/CSS</span>
                    <div className="skills-percent">
                        <div data-aos="slide-right" data-aos-mirror="true" data-aos-once="true" data-aos-offset="-100" style={{width: '90%'}} className="skills-progress"></div>
                    </div>
                    <span className="skills-Value">90%</span>
                </div>
            </div>
        </div>
    )
    }
}

export default Skills
