import React from 'react'
import './Header.css'

import { Link } from "react-scroll";

import DrawerToggleButton from '../sideDrawer/DrawerToggleButton'

const Header = props => (
    <header className="toolbar">
        <nav className="toolbar_navigation">            
            <div className="toolbar_name">
                <span className="name">SAYAK GHOSH</span>
                <span className="profession">Software Engineer</span>
            </div>
            <div className="spacer" />
            <div className="toolbar_navigation-items">
                <ul>
                    <Link
                    activeClass="active"
                    to="home"
                    spy={true}
                    smooth={true}
                    offset={-70}
                    duration={1000}>
                        <li><span>Home</span></li>
                    </Link>
                    <Link
                    activeClass="active"
                    to="about"
                    spy={true}
                    smooth={true}
                    offset={-70}
                    duration={1000}>
                        <li><span>About Me</span></li>
                    </Link>
                    <Link
                    activeClass="active"
                    to="academics"
                    spy={true}
                    smooth={true}
                    offset={-70}
                    duration={1000}>
                        <li><span>Academics</span></li>
                    </Link>
                    <Link
                    activeClass="active"
                    to="work"
                    spy={true}
                    smooth={true}
                    offset={-70}
                    duration={1000}>
                        <li><span>Experiences</span></li>
                    </Link>
                    <Link
                    activeClass="active"
                    to="skills"
                    spy={true}
                    smooth={true}
                    offset={-70}
                    duration={1000}>
                        <li><span>Skills</span></li>
                    </Link>
                    {/* <Link
                    activeClass="active"
                    to="projects"
                    spy={true}
                    smooth={true}
                    offset={-70}
                    duration={1000}>
                        <li><span>Projects</span></li>
                    </Link> */}
                    <Link
                    activeClass="active"
                    to="contact"
                    spy={true}
                    smooth={true}
                    offset={-70}
                    duration={1000}>
                        <li><span>Contact Me</span></li>
                    </Link>
                </ul>
            </div>
            <div className="toolbar__toggle-button">
                <DrawerToggleButton click={props.drawerClickhandler} />
            </div>
        </nav>
    </header>
);

export default Header
