import React, { Component } from 'react'
import './contact.scss'

const initialState = {
    firstname: '',
    lastname: '',
    mobile: '',
    email: '',
    message: '',
    errorFirstname: '',
    errorLastname: '',
    errorEmail: '',
    errorMobile: '',
    errorMessage: '',
    showThanks: false
}

class Contact extends Component {
    constructor(props) {
        super(props)    
        this.state = initialState
    }

    handleFirstName = (event) => {
        this.setState({ firstname: event.target.value})
    }

    handleLastName = (event) => {
        this.setState({ lastname: event.target.value})
    }

    handleMobile = (event) => {
        this.setState({ mobile: event.target.value})
    }

    handleEmail = (event) => {
        this.setState({ email: event.target.value})
    }

    handleMessage = (event) => {
        this.setState({ message: event.target.value})
    }

    validate = () => {
        let errorFirstname = ''
        let errorLastname = ''
        let errorEmail = ''
        let errorMobile = ''
        let errorMessage = ''
        const mobregex = new RegExp(/^((\+)?(\d{2}[-]))?(\d{10}){1}?$/)
        const emailregex = new RegExp(/^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/)
        const nameregex = new RegExp(/^[\w'\-,.][^0-9_!¡?÷?¿/\\+=@#$%ˆ&*(){}|~<>;:[\]]{1,}$/)
        if(!this.state.firstname){
            errorFirstname = '! Kindly enter your Firstname'
        }
        if(!this.state.lastname){
            errorLastname = '!Kindly enter your Lastname'
        }
        if(!this.state.mobile){
            errorMobile = '! Kindly enter your Mobile Number'
        }
        if(!this.state.email){
            errorEmail = '! Kindly enter your E-mail Address'
        }
        if(!this.state.message){
            errorMessage = '! Kindly enter your Message for me'
        }
        if(!nameregex.test(this.state.firstname)){
            errorFirstname = '! Kindly enter a valid First Name'
        }
        if(!nameregex.test(this.state.lastname)){
            errorLastname = '! Kindly enter a valid Last Name'
        }
        if(!mobregex.test(this.state.mobile)){
            errorMobile = '! Please enter a valid Mobile Number'
        }
        if(!emailregex.test(this.state.email)){
            errorEmail = '! Please enter a valid E-mail Address'
        }
        if(errorFirstname || errorMobile || errorEmail || errorLastname || errorMessage){
            this.setState({errorFirstname, errorLastname, errorEmail, errorMobile, errorMessage})
            return false
        }

        return true
    }

    handleSubmit = event => {
        event.preventDefault()
        const isValid = this.validate();
        if(isValid){
            console.log(this.state)
            this.setState(initialState)
            this.setState({showThanks: true})
        }
    }
    
    render() {
        return (
            <div id="contact" className="contact-main-container">
                <form onSubmit={this.handleSubmit} className="contact-container">
                    <h2>-- Keep in Touch --</h2>
                    <div className="row100">
                        <div className="col">
                            <div className="inputBox">
                                <input type="text" name=""
                                value={this.state.firstname}
                                onChange={this.handleFirstName} required />
                                <span className="text">First Name</span>
                                <span className="line"></span>
                            </div>
                            {this.state.errorFirstname ? 
                            <span className="errorMees">{this.state.errorFirstname}</span> : null
                            }
                        </div>
                        <div className="col">
                            <div className="inputBox">
                                <input type="text" name=""
                                value={this.state.lastname}
                                onChange={this.handleLastName} required/>
                                <span className="text">Last Name</span>
                                <span className="line"></span>        
                            </div>
                            {this.state.errorLastname ? 
                            <span className="errorMees">{this.state.errorLastname}</span> : null
                            }
                        </div>
                    </div>
                    <div className="row100">
                        <div className="col">
                            <div className="inputBox">
                                <input type="text" name=""                                
                                value={this.state.email}
                                onChange={this.handleEmail} required/>
                                <span className="text">E-mail</span>
                                <span className="line"></span>
                            </div>
                            {this.state.errorEmail ? 
                            <span className="errorMees">{this.state.errorEmail}</span> : null
                            }
                        </div>
                        <div className="col">
                            <div className="inputBox">
                                <input type="text" name=""                                
                                value={this.state.mobile}
                                onChange={this.handleMobile} required/>
                                <span className="text">Mobile</span>
                                <span className="line"></span>
                            </div>
                            {this.state.errorMobile ? 
                            <span className="errorMees">{this.state.errorMobile}</span> : null
                            }
                        </div>
                    </div>
                    <div className="row100">
                        <div className="col">
                            <div className="inputBox textarea">
                                <textarea                                
                                value={this.state.message}
                                onChange={this.handleMessage} required></textarea>
                                <span className="text">Your message...</span>
                                <span className="line"></span>
                            </div>
                            {this.state.errorMessage ? 
                            <span className="errorMees">{this.state.errorMessage}</span> : null
                            }
                        </div>                        
                    </div>
                    <div className="row100">
                        <div className="col">
                            <input className="contactmebutton" type="submit" value="Contact Me" />
                            {this.state.showThanks ? 
                            <span className="contact-thanks">Thank you for your Message. Will get back to you soon <span role="img" aria-label="smiley">😀</span></span> : null
                            }
                            
                        </div>
                    </div>
                </form>
            </div>
        )
    }
}

export default Contact
