import React, { Component } from 'react'
import './home.scss'

import { Link } from "react-scroll";

import Avatar from './myAvatar.svg'

class Home extends Component {

    render(){
        return (
            <div id="home" className="container">
                <div className="card">
                    <div className="img-avatar">
                        <img className="avatar" src={Avatar} alt="Avatar" />
                    </div>
                    <div className="card-text">
                        <div className="portada">
                        
                        </div>
                        <div className="title-total">   
                            <div className="title">Software Engineer</div><br/>
                            <span className="card-name">SAYAK GHOSH</span>
                            <span className="card-degree">Bachelor in Technology | Information Technology</span>
                            <div className="sp"></div>
                            <a href="./Sayak's Resume.pdf" download className="resume">
                                Download Resume
                            </a>
                        </div>                    
                    </div>                 
                </div>
                <Link
                    activeClass="active"
                    to="about"
                    spy={true}
                    smooth={true}
                    offset={-70}
                    duration={1000}
                >
                    <div className="continue-buttons">
                        <div className="continue-container">
                        <span className="btn effect04" data-sm-link-text="LET'S GO..."><span>CONTINUE</span></span>            
                        </div>
                    </div>
                </Link>
            </div>
        )
    }
}

export default Home

