import React from 'react'

import AOS from 'aos';
import '../../../node_modules/aos/dist/aos.css'

import './about.scss'

class Home extends React.Component {
    constructor(props, context) { 
        super(props, context); 
        AOS.init({
            duration: 2000
        }); 
      } 

    render(){
        return (
    
        <div id="about" className="about-main-container">
            <div className="about-left-container">
                <h1 className="description-about-me">
                    <p data-aos="fade-right" data-aos-mirror="true" data-aos-once="true" className="underlined">A 
                    Software Engineer and Developer, enthusiastic about Full Stack Development and implementation of Machine Learning 
                    in Web and Mobile Applications...</p>
                    {/* <br/><br/> */}
                    <p data-aos="fade-in" data-aos-mirror="true" data-aos-once="true" className="underlined underlined--gradient">...Currently working as a Programmer Analyst Trainee at Cognizant Technology Solutions.</p>
                    {/* <br/><br/> */   }
                    <p data-aos="fade-left" data-aos-mirror="true" data-aos-once="true" className="underlined underlined--reverse">A smart engineer and a hard worker, trying to better the world of Software Development !!!</p>
                </h1>
            </div>
            <div className="about-right-container">
                    <div className="neons">
                        <h1 className="about-me"><em>ABOUT<br/>ME</em></h1>
                    </div>
            </div>
        </div>
        
        )
    }
}

export default Home
