import React, { Component } from 'react'

import './academics.scss'



import AOS from 'aos';
import '../../../node_modules/aos/dist/aos.css'

class Academics extends Component {
    constructor(props, context) { 
        super(props, context); 
        AOS.init({
            duration: 2000
        }); 
      } 

    render() {
        return (
            <div id="academics" className="academics-main-cont">
                <div className="academics-left-container">
                    <span data-aos="fade-out" data-aos-mirror="true" data-aos-once="true" className="aca-header">
                        EDUCATION<br/>
                    </span>
                </div>
                <div className="academics-right-container">
                    <div data-aos="fade-down" data-aos-delay="40" data-aos-mirror="true" data-aos-once="true" className="courses-container">
                        <div className="course">
                            <div className="course-preview">
                                <h6>Course</h6>
                                <h2>Bachelor in Technology</h2>
                                <h5>2016 - 2020</h5>
                            </div>
                            <div className="course-info">
                                <div className="progress-container">
                                    <div className="progress"></div>
                                    <span className="progress-text">
                                        Completed
                                    </span>
                                </div>
                                <h6>INSTITUTION</h6>
                                <h2>SRM INSTITUTE OF SCIENCE AND TECHNOLOGY</h2>
                                <h5> Major:- Information Technology</h5>
                                <h5>Marks:- 75.46 %</h5>
                            </div>
                        </div>
                    </div>
                    <div data-aos="fade-in" data-aos-delay="40" data-aos-mirror="true" data-aos-once="true" className="courses-container">
                        <div className="course">
                            
                            <div className="course-info">
                                <div className="progress-container">
                                    <div className="progress"></div>
                                    <span className="progress-text">
                                        Completed
                                    </span>
                                </div>
                                <h6>INSTITUTION</h6>
                                <h2>DON BOSCO SCHOOL, LILUAH</h2>
                                <h5>Major:- Science</h5>
                                <h5>Marks:- 92.75 %</h5>
                            </div>
                            <div className="course-preview">
                                <h6>Course</h6>
                                <h2>ISC / 12<sup>th</sup> Standard</h2>
                                <h5>2016</h5>
                            </div>
                        </div>
                    </div>
                    <div data-aos="slide-up" data-aos-delay="40" data-aos-mirror="true" data-aos-once="true" className="courses-container">
                        <div className="course">
                            <div className="course-preview">
                                <h6>Course</h6>
                                <h2>ICSE / 10<sup>th</sup> Standard</h2>
                                <h5>2014</h5>
                            </div>
                            <div className="course-info">
                                <div className="progress-container">
                                    <div className="progress"></div>
                                    <span className="progress-text">
                                        Completed
                                    </span>
                                </div>
                                <h6>INSTITUTION</h6>
                                <h2>DON BOSCO SCHOOL, LILUAH</h2>
                                <h5>Marks:- 88.20 %</h5>
                            </div>
                        </div>
                    </div>
                </div> 
            </div>
        )
    }
}

export default Academics
