import React from 'react'
import './sideDrawer.css'

import { Link } from "react-scroll";

class SideDrawer extends React.Component{    
    
    render(){
    let drawerClasses = 'side-drawer'
    if(this.props.show){
        drawerClasses = 'side-drawer open'
    }
    return (
    <nav className={drawerClasses}>
        <ul>
            <li>
            <Link
                activeClass="active"
                to="home"
                spy={true}
                smooth={true}
                offset={-70}
                duration={500}
            >
                <span>Home</span>
            </Link>
            </li>
            <li>
            <Link
                activeClass="active"
                to="about"
                spy={true}
                smooth={true}
                offset={-70}
                duration={500}
            >
                <span>About Me</span>
            </Link>
            </li>
            <li>
            <Link
                activeClass="active"
                to="academics"
                spy={true}
                smooth={true}
                offset={-70}
                duration={500}
            >
                <span>Academics </span>
            </Link>
            </li>
            <li>
            <Link
                activeClass="active"
                to="work"
                spy={true}
                smooth={true}
                offset={-70}
                duration={500}
            >
                <span>Experiences</span>
            </Link>
            </li>
            <li>
            <Link
                activeClass="active"
                to="skills"
                spy={true}
                smooth={true}
                offset={-70}
                duration={500}
            >
                <span>Skills</span>
            </Link>
            </li>
            {/* <li>
            <Link
                activeClass="active"
                to="projects"
                spy={true}
                smooth={true}
                offset={-70}
                duration={500}
            >
                <span>Projects</span>
            </Link>
            </li> */}
            <li>
            <Link
                activeClass="active"
                to="contact"
                spy={true}
                smooth={true}
                offset={-70}
                duration={500}
            >
                <span>Contact Me</span>
            </Link>
            </li>
        </ul>
    </nav>
    )
    }
} 

export default SideDrawer;