import React, { Component } from 'react'
import './work.scss'

import AOS from 'aos';
import '../../../node_modules/aos/dist/aos.css'

class Work extends Component {
    constructor(props, context) { 
        super(props, context); 
        AOS.init({
            duration: 1000
        }); 
      } 

    curlyStart = () => {
        return '{'
      }
      
    curlyEnd = () => {
        return '}'
      }

    render() {
        return (
            <div id="work" className="work-main-container">
                <div className="work-left-container">
                    <div className="work-left-cont">
                        <div className="work-text-box">
                            <h1 data-aos="slide-right" data-aos-delay="200" data-aos-mirror="true" data-aos-once="true" className="work-h1">Work Experiences</h1>
                            <h1 data-aos="slide-right" data-aos-delay="100" data-aos-mirror="true" data-aos-once="true" className="work-h1">Work Experiences</h1>
                        </div>
                    </div>
                </div>
                <div className="work-right-containerr">
                    <div className="workk-content">
                        <div data-aos="fade-up" data-aos-delay="40" data-aos-mirror="true" data-aos-once="true" className="workk-card">
                            <div className="workk-card__side workk-card__side--front">
                                <div className="workk-card__cont">
                                    <span className="blue"><code>&lt;h1&gt;</code></span><span><span className="green">PROGRAMMER ANALYST TRAINEE</span>
                                    <span className="blue"><code>&lt;/h1&gt;</code></span></span>
                                </div>
                            </div>
                            <div className="workk-card__side workk-card__side--back">
                                <div className="workk-card__cta">
                                    <p><span className="purple">const</span> description <span className="cyan">=</span> {this.curlyStart()}
                                    <br />
                                    <span className="space red">Company</span><span className="cyan">:</span> <span className="green">'Cognizant Technology Solutions'</span>,
                                    <br/>
                                    <span className="space red">Location</span><span className="cyan">:</span> <span className="green">'Chennai, Tamil Nadu</span>',
                                    <br/>
                                    <span className="space red">Duration</span><span className="cyan">:</span> <span className="green">'Aug, 2020 - Current'</span>,
                                    <br/>
                                    <span className="space red">E-mail</span><span className="cyan">:</span> <span className="green">'Sayak.Ghosh@cognizant.com'</span>
                                    <br/> {this.curlyEnd()};
                                    </p>
                                </div>
                            </div>
                        </div>
                    
                    
                        <div data-aos="fade-up" data-aos-delay="70" data-aos-mirror="true" data-aos-once="true" className="workk-card">
                            <div className="workk-card__side workk-card__side--front">
                                <div className="workk-card__cont">
                                    <span className="blue"><code>&lt;h1&gt;</code></span><span><span className="green">SAP REVIEW INTERN</span>
                                    <span className="blue"><code>&lt;/h1&gt;</code></span></span>
                                </div>
                            </div>
                            <div className="workk-card__side workk-card__side--back">
                                <div className="workk-card__cta">
                                    <p><span className="purple">const</span> description <span className="cyan">=</span> {this.curlyStart()}
                                    <br />
                                    <span className="space red">Company</span><span className="cyan">:</span> <span className="green">'ITC Limited'</span>,
                                    <br/>
                                    <span className="space red">Location</span><span className="cyan">:</span> <span className="green">'Kolkata, West BEngal</span>',
                                    <br/>
                                    <span className="space red">Duration</span><span className="cyan">:</span> <span className="green">'Dec, 2018 - Jan, 2019'</span>,
                                    <br/>
                                    <span className="space red">Brief</span><span className="cyan">:</span> <span className="green">'Knowledge
                                     on various aspects of SAP and ERP business was acquired and review on SAP codes and their uses by employees was carried out.'</span>
                                    <br/> {this.curlyEnd()};
                                    </p>
                                </div>
                            </div>
                        </div>

                        <div data-aos="fade-up" data-aos-delay="100" data-aos-mirror="true" data-aos-once="true" className="workk-card">
                            <div className="workk-card__side workk-card__side--front">
                                <div className="workk-card__cont">
                                    <span className="blue"><code>&lt;h1&gt;</code></span><span><span className="green">WEB DEVELOPMENT TRAINEE</span>
                                    <span className="blue"><code>&lt;/h1&gt;</code></span></span>
                                </div>
                            </div>
                            <div className="workk-card__side workk-card__side--back">
                                <div className="workk-card__cta">
                                    <p><span className="purple">const</span> description <span className="cyan">=</span> {this.curlyStart()}
                                    <br />
                                    <span className="space red">Company</span><span className="cyan">:</span> <span className="green">'Globsyn Technologies'</span>,
                                    <br/>
                                    <span className="space red">Location</span><span className="cyan">:</span> <span className="green">'Kolkata, West BEngal</span>',
                                    <br/>
                                    <span className="space red">Duration</span><span className="cyan">:</span> <span className="green">'May, 2018'</span>,
                                    <br/>
                                    <span className="space red">Brief</span><span className="cyan">:</span> <span className="green">'Training was
                                     given on HTML, CSS, PHP and MySQL. Using the above technologies and JavaScript, an Online Food Delivery Web Application was made.'</span>
                                    <br/> {this.curlyEnd()};
                                    </p>
                                </div>
                            </div>
                        </div>

                    </div>
                </div>
            </div>
        )
    }
}

export default Work
